.container > div > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: center;
      justify-content: center;
  cursor: pointer;
}

.text {
  pointer-events: none;
  -ms-flex-pack: start !important;
      justify-content: start !important;
  font-family: 'Kanit', sans-serif;
  line-height: 0px;
  text-transform: uppercase;
}

.number {
  font-size: 450px;
  color: #373c4c;
}

.number span {
  display: inline-block;
  position: relative;
  -webkit-transform: translate3d(-35%, 0, 0);
          transform: translate3d(-35%, 0, 0);
}

.header {
  margin-left: 350px;
  font-size: 50px;
  color: white;
}

.stripe {
  height: 2px;
  width: auto;
}

.slopeBegin {
  background-color: #20232f;
  -webkit-clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
          clip-path: polygon(20vw 0, 70% 0, calc(70% - 20vw) 100%, 0 100%);
}

.slopeEnd {
  -webkit-clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
          clip-path: polygon(70% 0, 100% 0, calc(100% - 20vw) 100%, calc(70% - 20vw) 100%);
}

.slopeBegin,
.slopeEnd {
  position: absolute;
  width: 140%;
  height: 100%;
  cursor: pointer;
}

.pink {
  background: -webkit-linear-gradient(left, deeppink 0%, coral 100%);
  background: -o-linear-gradient(left, deeppink 0%, coral 100%);
  background: -webkit-gradient(linear, left top, right top, from(deeppink), to(coral));
  background: linear-gradient(to right, deeppink 0%, coral 100%);
}

.teal {
  background: -webkit-linear-gradient(left, SlateBlue 0%, DeepSkyBlue 100%);
  background: -o-linear-gradient(left, SlateBlue 0%, DeepSkyBlue 100%);
  background: -webkit-gradient(linear, left top, right top, from(SlateBlue), to(DeepSkyBlue));
  background: linear-gradient(to right, SlateBlue 0%, DeepSkyBlue 100%);
}

.tomato {
  background: -webkit-linear-gradient(left, tomato 0%, gold 100%);
  background: -o-linear-gradient(left, tomato 0%, gold 100%);
  background: -webkit-gradient(linear, left top, right top, from(tomato), to(gold));
  background: linear-gradient(to right, tomato 0%, gold 100%);
}

.potato {
  background: -webkit-linear-gradient(left, #a8e063 0%, #56ab2f 100%);
  background: -o-linear-gradient(left, #a8e063 0%, #56ab2f 100%);
  background: -webkit-gradient(linear, left top, right top, from(#a8e063), to(#56ab2f));
  background: linear-gradient(to right, #a8e063 0%, #56ab2f 100%);
}

a:link {
    text-decoration: inherit;
    color: inherit;
    padding-bottom: 3px;
    cursor: pointer;
}

a:visited {
    text-decoration: inherit;
    color: inherit;
    padding-bottom: 3px;
    cursor: pointer;
}

.anchor {
  border-bottom: 1px solid #fff;
}

i {
  margin-right: 20px;
  margin-left: 15px;
  font-size: 20px;
  cursor: pointer;
}



